"use client";
import { useEffect } from "react";

export default function Page() {
    const test = async () => {
        const res = await fetch("http://localhost:3333/testCookie");
        const data = await res.json();
        console.log(data);
    };
    useEffect(() => {
        test();
    }, []);
    return <div>testing cookie</div>;
}
